@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #353535;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  height: 100%;
  background-color: #f0f0ea;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.hidden {
  display: none;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.footer {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 30px;
  padding-top: 30px;
  border-top: solid 1px #cccccc;
}
.footer__contacts {
  background-color: inherit;
}

.copyright {
  font-size: 12px;
}
.copyright__link:hover {
  opacity: 0.6;
}
.copyright__link:focus {
  outline: none;
  box-shadow: 0 0 2px 2px #ff6547;
}
.copyright__link--image {
  display: block;
}
.copyright__link--text {
  color: #353535;
}

.contacts {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.contacts__link {
  display: block;
  width: 29px;
  height: 29px;
  margin-left: 10px;
  font-size: 0;
  background-image: url("../img/svg/sprite-social.svg");
  background-repeat: no-repeat;
  background-size: 29px 274px;
  cursor: pointer;
}
.contacts__link:focus {
  outline: none;
  box-shadow: 0 0 2px 2px #ff6547;
}
.contacts__link--twitter {
  background-position: 0 0;
}
.contacts__link--twitter:hover, .contacts__link--twitter:focus {
  background-position: 0 -140px;
}
.contacts__link--instagram {
  background-position: 0 -35px;
}
.contacts__link--instagram:hover, .contacts__link--instagram:focus {
  background-position: 0 -175px;
}
.contacts__link--facebook {
  background-position: 0 -70px;
}
.contacts__link--facebook:hover, .contacts__link--facebook:focus {
  background-position: 0 -210px;
}
.contacts__link--vk {
  background-position: 0 -105px;
}
.contacts__link--vk:hover, .contacts__link--vk:focus {
  background-position: 0 -245px;
}

.popup__avatar {
  position: absolute;
  bottom: 100%;
  left: 0;
  margin-bottom: 10px;
}
.popup__close {
  position: absolute;
  bottom: 100%;
  right: 0;
  padding: 0;
  font-size: 0;
  border: 0;
  background: none;
}
.popup__close:active {
  transform: translate(1px, 1px);
}
.popup__close:focus {
  box-shadow: 0 0 10px 3px #ff6547;
}
.popup__close::after {
  font-size: 50px;
  line-height: 40px;
  display: block;
  content: "×";
  color: #000000;
}
.popup__title {
  margin: 0;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 18px;
}
.popup__text {
  margin: 0;
  margin-bottom: 10px;
}
.popup__text--price {
  font-size: 22px;
  font-weight: 700;
}
.popup__text--capacity {
  margin-bottom: 0;
}
.popup__price span {
  font-size: 16px;
}
.popup__type {
  margin: 0;
  margin-bottom: 5px;
}
.popup__features {
  margin: 0 0 10px;
  padding: 0;
  list-style: none;
}
.popup__feature {
  display: inline-block;
  overflow: hidden;
  width: 30px;
  height: 28px;
  font-size: 0;
  background-image: url("../img/svg/sprite-feature.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25px 345px;
}
.popup__feature--wifi {
  background-position: 2px 5px;
}
.popup__feature--dishwasher {
  background-position: 2px -25px;
}
.popup__feature--parking {
  background-position: 3px -55px;
}
.popup__feature--washer {
  background-position: 2px -85px;
}
.popup__feature--elevator {
  background-position: 2px -143px;
}
.popup__feature--conditioner {
  background-position: 3px -114px;
}
.popup__photos {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.popup__photo {
  margin-right: 5px;
  margin-bottom: 5px;
}
.popup__photo:hover {
  border: 2px solid #ff5635;
}

.success,
.error {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow: auto;
  padding-top: 300px;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.8);
}

.success__message,
.error__message {
  position: relative;
  color: #ffffff;
  font-size: 50px;
  font-weight: 700;
}

.success__message::before,
.error__message::before {
  content: "";
  position: absolute;
  top: -150px;
  left: 50%;
  width: 109px;
  height: 122px;
  background-image: url("../img/svg/muffin-white.svg");
  background-position: center;
  background-repeat: no-repeat;
  transform: translateX(-50%);
}

.error__message::before {
  background-image: url("../img/svg/muffin-red.svg");
}

.error__button {
  padding: 10px;
  font-size: 22px;
  font-weight: 700;
  box-sizing: content-box;
  color: white;
  border: 4px solid #ff5635;
  background-color: #ff5635;
  border-radius: 8px;
}

.error__button:hover,
.error__button:focus {
  opacity: 0.8;
}

.error__button:focus {
  outline: none;
}

.error__button:active {
  outline: none;
  opacity: 0.6;
}

.ad-form {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.ad-form__field {
  display: flex;
  width: 150px;
}
.ad-form__element {
  flex-basis: 25%;
  min-height: 90px;
  font-size: 14px;
}
.ad-form__element input,
.ad-form__element select,
.ad-form__element textarea {
  width: calc(100% - 20px);
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  height: 42px;
  padding-left: 14px;
  background-color: white;
  border: 1px solid #d9d9d3;
  border-radius: 4px;
}
.ad-form__element input:hover,
.ad-form__element select:hover,
.ad-form__element textarea:hover {
  border-color: #ffaa99;
}
.ad-form__element input :focus,
.ad-form__element select :focus,
.ad-form__element textarea :focus {
  outline: none;
  box-shadow: 0 0 2px 2px #ff6547;
}
.ad-form__element--wide {
  flex-basis: 50%;
  max-width: 50%;
}
.ad-form__element--time {
  flex-basis: 50%;
}
.ad-form__element--time select {
  width: calc(50% - 20px);
}
.ad-form__element--time select:first-of-type {
  margin-right: 14px;
}
.ad-form__element--submit {
  flex-basis: 100%;
  padding: 40px 0;
  text-align: center;
}
.ad-form__label {
  display: block;
  padding-bottom: 5px;
}
.ad-form textarea {
  width: calc(100% - 20px);
  max-width: calc(100% - 20px);
  min-height: 230px;
  padding-top: 10px;
}
.ad-form fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}
.ad-form__photo-container {
  display: flex;
  flex-wrap: wrap;
}
.ad-form__upload {
  display: flex;
  margin-right: 10px;
  width: 150px;
  min-height: 70px;
}
.ad-form__upload:last-of-type {
  margin-right: 0;
}
.ad-form__upload .ad-form__input {
  width: auto;
}
.ad-form__drop-zone {
  width: 100%;
  height: 70px;
  padding: 20px 10px 5px;
  text-align: center;
  font-size: 14px;
  color: #999999;
  border: dashed 1px #c7c7c7;
  border-radius: 5px;
  cursor: pointer;
}
.ad-form__drop-zone:hover {
  color: #ff5635;
}
.ad-form__drop-zone:focus, .ad-form__drop-zone:focus + .ad-form__drop-zone {
  color: #ff5635;
  box-shadow: 0 0 2px 2px #ff6547;
}
.ad-form__photo {
  margin-right: 10px;
  margin-bottom: 10px;
  width: 70px;
  height: 70px;
  background-color: #e4e4de;
  border-radius: 5px;
}
.ad-form__submit {
  font-size: 22px;
  font-weight: 700;
  box-sizing: content-box;
  width: 206px;
  height: 60px;
  background-color: white;
  border: 4px solid #ffaa99;
  border-radius: 8px;
}
.ad-form__submit:hover, .ad-form__submit:focus {
  border-color: #ff6d51;
}
.ad-form__submit:focus {
  outline: none;
}
.ad-form__submit:active {
  outline: none;
  color: white;
  border-color: #ff6d51;
  background-color: #ff6d51;
}
.ad-form__reset {
  margin: 0;
  padding: 0;
  font-size: 14px;
  text-decoration: underline;
  border: 0;
  background: none;
  cursor: pointer;
}
.ad-form__reset:focus {
  outline: none;
  box-shadow: 0 0 2px 2px #ff6547;
}
.ad-form--disabled {
  opacity: 0.3;
}

.ad-form-header {
  width: 100%;
  padding-bottom: 20px;
}
.ad-form-header__title {
  margin-bottom: 10px;
}
.ad-form-header__upload {
  display: flex;
  align-items: center;
}
.ad-form-header__preview {
  display: flex;
  align-items: center;
  margin-right: 10px;
  padding: 0 15px;
  width: 70px;
  height: 70px;
  text-align: center;
  border-radius: 5px;
  background-color: #dadada;
}
.ad-form-header__info {
  margin-top: 0;
  margin-left: 20px;
}
.ad-form-header__drop-zone {
  height: 70px;
  padding: 20px 10px 5px;
  text-align: center;
  font-size: 14px;
  color: #999999;
  border: dashed 1px #c7c7c7;
  border-radius: 5px;
  cursor: pointer;
}
.ad-form-header__drop-zone:hover {
  color: #ff5635;
}
.ad-form-header__drop-zone:focus, .ad-form-header__drop-zone:focus + .ad-form-header__drop-zone {
  color: #ff5635;
  box-shadow: 0 0 2px 2px #ff6547;
}

.promo {
  position: absolute;
  top: 40px;
  left: 50%;
  z-index: 1;
  width: 211px;
  height: 45px;
  transform: translateX(-50%);
}

.map {
  position: relative;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  overflow: hidden;
  height: 750px;
  background-color: #82bcc0;
  background-image: url("../img/map.jpg");
  background-repeat: no-repeat;
  background-position: center bottom;
  border-radius: 0 0 10px 10px;
  /**
  * Фильтры объявления
  */
}
.map__pins {
  position: relative;
  width: 100%;
  flex-grow: 1;
}
.map__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: opacity 300ms ease-in;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.3);
}
.map__title {
  width: 100%;
  margin: 0;
  padding: 0;
  margin-top: 170px;
  font-size: 85px;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
}
.map__filters-container {
  width: 100%;
  background-color: rgba(3, 28, 45, 0.5);
}
.map__filters {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 960px;
  height: 46px;
  margin: 0 auto;
  transition: opacity 300ms ease-in;
  opacity: 1;
}
.map__filter {
  height: 30px;
  margin-right: 10px;
  font-size: 14px;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid transparent;
}
.map__filter:hover {
  border: 1px solid #ffaa99;
}
.map__filter:focus {
  outline: none;
  box-shadow: 0 0 2px 2px #ff6547;
}
.map__feature {
  display: inline-block;
  overflow: hidden;
  width: 30px;
  height: 28px;
  font-size: 0;
  vertical-align: middle;
  cursor: pointer;
  background-color: #f8f8f8;
  background-image: url("../img/svg/sprite-feature.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25px 345px;
  border-radius: 3px;
}
.map__card {
  position: absolute;
  z-index: 2;
  top: 150px;
  left: 20px;
  padding: 10px 10px 20px;
  width: 230px;
  font-size: 12px;
  border-radius: 5px;
  background-color: #ffffff;
}
.map.map--faded .map__overlay {
  opacity: 1;
}
.map.map--faded .map__filters {
  opacity: 0;
}

/**
* Метка объявления
*/
.map__pin {
  position: absolute;
  z-index: 1;
  width: 50px;
  height: 70px;
  margin: 0;
  padding: 0;
  border: 0;
  background: none;
}

.map__pin:focus {
  outline: none;
  filter: drop-shadow(0 0 5px red);
}

.map__pin img {
  position: absolute;
  z-index: 1;
  top: 5px;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 50%;
}

.map__pin:not(.map__pin--main)::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 70px;
  top: 0;
  left: 0;
  background-image: url("../img/svg/pins.svg");
  background-repeat: no-repeat;
  background-position: 0 -9px;
}

.map__pin:not(.map__pin--main):hover::after,
.map__pin:not(.map__pin--main):active::after,
.map__pin--active:not(.map__pin--main)::after {
  background-position: 0 -88px;
}

.map__pin:active img,
.map__pin--active img {
  box-shadow: 0 0 15px 15px rgba(255, 86, 53, 0.7);
}

.map__pin--main {
  width: 65px;
  height: 65px;
  cursor: move;
}

.map__pin--main img {
  position: relative;
  z-index: 1;
  padding: 2px 6px 6px;
  transform: translateY(-7px);
  border: solid 5px #ff5635;
  border-radius: 50%;
  background-color: #ffffff;
  box-sizing: content-box;
}

.map__pin--main:active img,
.map__pin--main--active img {
  background-color: #ffffff;
}

.map__pin--main svg {
  position: absolute;
  z-index: 0;
  top: -50px;
  left: -47px;
  width: 156px;
  height: 156px;
  transition: transform 300ms ease-in;
  transform: rotate(120deg) scale(0);
  transform-origin: 78px 78px;
}

.map__pin--main:hover ellipse {
  fill: rgba(255, 86, 53, 0.9);
}

.map__pin--main::after {
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  content: "";
  border: solid 5px transparent;
  border-top: solid 18px #fff;
  border-bottom: 0;
  transition: transform 300ms ease-in;
  transform: translate(-50%, -6px);
  border-top-width: 22px;
  border-top-color: #ff5635;
}

.map--faded .map__pin--main svg {
  transform: rotate(-20deg) scale(1);
}

.map--faded .map__pin--main::after {
  transform: translate(-50%, -30px);
}

.map__pin--main text {
  font-size: 7px;
  font-weight: 700;
  fill: #ffffff;
}

